import React, { Component } from 'react';
import Axios from 'axios';
import logo from './logo.svg';
import './App.css';

import StoryList from './components/story-list/storyList';
import StoryButton from './components/story-button/storyButton';
import StoryModal from './components/story-modal/storyModal';

class App extends Component {
  constructor(){
    super();

    this.state = {
      stories: [],
      modalIsOpen: false,
      story: {
        author: '',
        content: '',
        _id: undefined
      }
    }

    this.apiUrl = 'https://wt-c5f60bd607a68a988e116f409e75d7dd-0.run.webtask.io/webtask-mern-api/stories';
    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.handleEdit = this.handleEdit.bind(this);
    this.handleDelete = this.handleDelete.bind(this)
  }

  componentDidMount(){
    // Fetch stories from API and update the state
    Axios.get(this.apiUrl).then(({data}) => {
      console.log('Axios response');
      console.log(data);
      this.setState({stories: data});
    })
  }

  handleEdit(id){
    // Open a modal to update a story
    this.openModal(this.state.stories.find(x => x._id === id));
  }

  handleDelete(id){


    Axios.delete(`${this.apiUrl}?id=${id}`).then(() => {
      const updatedStories = this.state.stories.findIndex(x => x._id === id);
      this.setState({states: [...this.state.stories.splice(updatedStories, 1)]})
    })
  }
  openModal(story) {
    // Launches Modal. We will un-comment later
    this.setState({modalIsOpen: true});
    if(story) {
      this.setState({story});
    } 
  }

  closeModal(model) {
    this.setState({modalIsOpen: false});
    if(model) {
      this.setState({isLoading: true})
      if(!model._id) {
        console.log('Guardando Story');
        Axios.post(this.apiUrl, model).then(({data}) => {
          console.log(data);
          this.setState({stories: [data, ...this.state.stories]});
          this.setState({isLoading: false})
        })
      } else {
        Axios.put(`${this.apiUrl}?id=${model._id}`, model).then(({data}) => {
          const storyToUpdate = this.state.stories.find(x => x._id === model._id);
          const updatedStory = Object.assign({}, storyToUpdate, data)
          const newStories = this.state.stories.map(story => {
            if(data._id === story._id) return updatedStory;
            return story;
          })
          this.setState({stories: newStories});
          this.setState({isLoading: false})
        })
      }
    }
    this.setState({story: {
      author: '',
      content: '',
      _id: undefined
    }})
  }


  render() {
    return (
      <div className="App">
        <StoryModal
            modalIsOpen={this.state.modalIsOpen}
            story={this.state.story}
            closeModal={this.closeModal}
        />
        <div className="col-md-4 col-md-offset-4 Story">
          <div className="Story-header">
            <img src={logo} className="App-logo" alt="logo" />
            <h2>Stories</h2>
          </div>
          <StoryList
            stories={this.state.stories}
            handleEdit={this.handleEdit}
            handleDelete={this.handleDelete}
          />
          <div className="Story-footer">
            <p> Story app. A project following scotch.io </p>
          </div>
        </div>
        <StoryButton handleClick={this.openModal.bind(this, null)} />
      </div>
    );
  }
}

export default App;



