import React from 'react';
import './storyItem.css';

export default class StoryItem extends React.Component {
    render(){
        const {
            story,
            handleEdit,
            handleDelete
        } = this.props;

        return (
            <div className="Story-item clearfix">
                <div className="col-sm-9 Story-item-content">
                    <h4>{story.author}</h4>
                    <p>{story.content}</p>
                </div>
                <div className="col-sm-3 Story-item-control">
                    <span
                        className="glyphicon glyphicon-edit"
                        onClick={handleEdit.bind(this, story._id)}
                    />
                    <span
                        className="glyphicon glyphicon-remove"
                        onClick={handleDelete.bind(this, story._id)}
                    />
                </div>
            </div>
        )
    }
}


